
<template>
  <el-table
      :data="tableData"
      style="width: 100%"
      :row-class-name="tableRowClassName">
    <el-table-column
        prop="date"
        label="日期"
        width="180">
    </el-table-column>
    <el-table-column
        prop="name"
        label="姓名"
        width="180">
    </el-table-column>
    <el-table-column
        prop="address"
        label="地址">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: "StateTable",
  methods: {
    tableRowClassName({row, rowIndex}) {
      if (rowIndex === 1) {
        return 'warning-row';
      } else if (rowIndex === 3) {
        return 'success-row';
      }
      return '';
    }
  },
  data() {
    return {
      tableData: [{
        date: '2016-05-02',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园',
      }, {
        date: '2016-05-04',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }, {
        date: '2016-05-01',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园',
      }, {
        date: '2016-05-03',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }]
    }
  }
}
</script>

<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
</style>